<template>
  <md-app>
    <md-app-toolbar class="md-primary">
      <h1>時刻表</h1>
    </md-app-toolbar>
    <md-app-content>
      <DateTimeDropdown @changed-departure-date="onChangedDepartureDay" />
      <md-table>
        <md-table-toolbar>
          <h2 class="md-title">
            <b>バス時刻表</b>
          </h2>
        </md-table-toolbar>
        <md-table-row>
          <md-table-head v-for="head in busHeaders" v-bind:key="head">{{head}}</md-table-head>
        </md-table-row>
        <md-table-row v-for="col in busRecords" v-bind:key="col.id">
          <md-table-cell>{{col.time}}</md-table-cell>
        </md-table-row>
      </md-table>
      <md-table>
        <md-table-toolbar>
          <h2 class="md-titile">電車時刻表</h2>
        </md-table-toolbar>
        <md-table-row>
          <md-table-head v-for="head in trainHeaders" v-bind:key="head">{{head}}</md-table-head>
        </md-table-row>
        <md-table-row v-for="col in trainRecords" v-bind:key="col.id">
          <md-table-cell>{{col.time}}</md-table-cell>
          <md-table-cell v-bind:class="col.clazz">{{col.trainType}}</md-table-cell>
          <md-table-cell>{{col.destinationName}}</md-table-cell>
        </md-table-row>
      </md-table>
    </md-app-content>
  </md-app>
</template>

<script>
import {
  getShirayuriBusStop,
  getKeikyuTrain
} from "./components/time-table-model.js";
import moment from "moment";
import DateTimeDropdown from "./components/DateTimeDropdown";

export default {
  components: {
    DateTimeDropdown
  },
  // ロード時の処理
  // https://jp.vuejs.org/v2/guide/instance.html
  created: function() {
    this.busRecords = getShirayuriBusStop(moment(this.requestDate));
    this.trainRecords = getKeikyuTrain(moment(this.requestDate));
  },
  data() {
    return {
      // datetimepickerはStringで受け渡すためDateは不可
      requestDate: moment()
        .format("YYYY-MM-DD HH:mm")
        .toString(),
      // バス時刻ヘッダ
      busHeaders: ["発車時刻"],
      // バス時刻表 createdでデータ取得する
      busRecords: [],
      // 電車時刻表ヘッダ
      trainHeaders: ["発車時刻", "種別", "行き先"],
      // 電車時刻表  createdでデータ取得する
      trainRecords: []
    };
  },
  methods: {
    onChangedDepartureDay(value) {
      this.busRecords = getShirayuriBusStop(value);
      this.trainRecords = getKeikyuTrain(value);
    }
  }
};
</script>

