<template>
  <div class="md-layout md-gutter">
    <div class="md-layout-item md-size-33">
      <md-field>
        <label for="出発日">出発日</label>
        <md-select v-model="selectedDay" @md-selected="onChangeDate">
          <md-option
            v-for="departureDay in departureDays"
            :key="departureDay.id"
            :value="departureDay.id"
          >{{departureDay.date.format("MM/DD(ddd)")}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33">
      <md-field>
        <label for="時間">時間</label>
        <md-select v-model="selectedHour" @md-selected="onChangeDate">
          <md-option
            v-for="departureHour in departureHours"
            :key="departureHour"
            :value="departureHour"
          >{{departureHour}}</md-option>
        </md-select>
      </md-field>
    </div>
    <div class="md-layout-item md-size-33">
      <md-field>
        <label for="分">分</label>
        <md-select v-model="selectedMinute" @md-selected="onChangeDate">
          <md-option
            v-for="departureMinute in departureMinutes"
            :key="departureMinute"
            :value="departureMinute"
          >{{departureMinute}}</md-option>
        </md-select>
      </md-field>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import {
  makeDepartureDays,
  makeHoursArray,
  makeMinutesArray,
  zeroPading
} from "./common-utility";

export default {
  data() {
    return {
      selectedDay: null,
      selectedHour: null,
      selectedMinute: null,
      departureDays: [],
      departureHours: [],
      departureMinutes: []
    };
  },
  created() {
    this.selectedDay = moment()
      .startOf("days")
      .toDate()
      .getTime();
    this.selectedHour = zeroPading(moment().hours(), 2);
    this.selectedMinute = zeroPading(moment().minutes(), 2);
    this.selectedDate = moment();
    this.departureDays = makeDepartureDays();
    this.departureHours = makeHoursArray();
    this.departureMinutes = makeMinutesArray();
  },
  methods: {
    onChangeDate: function() {
      const date = moment(this.selectedDay);
      date.hours(this.selectedHour);
      date.minutes(this.selectedMinute);
      this.$emit("changed-departure-date", date);
    }
  }
};
</script>